import * as React from 'react';

type TPathViewContext = {
  activeTabs: any;
  setActiveTabs: React.Dispatch<React.SetStateAction<any>>;
};

export const PathViewContext = React.createContext<TPathViewContext | undefined>(undefined);

export const usePathViewContext = () => {
  const context = React.useContext(PathViewContext);
  if (context === undefined) {
    throw new Error('usePathViewContext must be used within a PathViewProvider');
  }
  return context;
};

type TSuccessModalProvider = {
  children: React.ReactNode;
};

export const PathViewProvider = ({ children }: TSuccessModalProvider) => {
  const [activeTabs, setActiveTabs] = React.useState(false);

  return <PathViewContext.Provider value={{ activeTabs, setActiveTabs }}>{children}</PathViewContext.Provider>;
};
