import * as React from 'react';
import Collapsible from '../../collapsible';
import { Switch, Button } from '@material-tailwind/react';
import { IoIosInformationCircleOutline } from 'react-icons/io';
import { IoEyeOutline, IoEyeOffOutline } from 'react-icons/io5';
import { LuTrash2 } from 'react-icons/lu';
import { FaPlus } from 'react-icons/fa6';
import DefaultImage from '../../../assets/svg/DefaultImage';
import { usePathViewContext } from '../../../context/pathViewContext';
import { useSuccessModalContext } from '../../../context/successModalContext';
import RichTextEditor from '../../richtexteditor';
import services from '../../../../../../../utils/services';
import InputFile from '../../InputFile';
import TagLine from '../../tagline';
import { toast } from 'react-toastify';

interface AboutImage {
  about_image_src: string;
}

interface Culture {
  title: string;
  body: string;
}

interface EmployeeTestimony {
  photo: string;
  title: string;
  subtitle: string;
  content: string;
}

interface Section1 {
  about_content: string;
  about_title: string;
  about_image: AboutImage[];
}

interface Section2 {
  head_title_1: string;
  head_subtitle_1: string;
  company_culture: { culture: Culture }[];
}

interface Section3 {
  head_title_3: string;
  head_subtitle_3: string;
  tag_title_3: string;
  employee_testimony: { testimony: EmployeeTestimony }[];
}

interface AboutUsData {
  section_1: Section1;
  section_2: Section2;
  section_3: Section3;
}

const initialValue: AboutUsData = {
  section_1: {
    about_content: '',
    about_title: '',
    about_image: [
      {
        about_image_src: '',
      },
    ],
  },
  section_2: {
    head_title_1: '',
    head_subtitle_1: '',
    company_culture: [
      {
        culture: {
          title: '',
          body: '',
        },
      },
    ],
  },
  section_3: {
    head_title_3: '',
    head_subtitle_3: '',
    tag_title_3: '',
    employee_testimony: [
      {
        testimony: {
          photo: '',
          title: '',
          subtitle: '',
          content: '',
        },
      },
    ],
  },
};

export default function AboutUs({ tabValue }: { tabValue: string }) {
  const [isOpenDialog, setIsOpenDialog] = React.useState({ status: false, type: '' });
  const [aboutUsData, setAboutUsData] = React.useState<AboutUsData>(initialValue);
  const [isLoadingGetData, setIsLoadingGetData] = React.useState<boolean>(true);
  const { activeTabs } = usePathViewContext();

  const getDataByValueTab = async () => {
    setIsLoadingGetData(true);
    try {
      const payload = {
        page_name: tabValue,
      };
      const response = await services.post('/setting/company_profile', payload);
      const result = response.data;
      setAboutUsData((prev) => ({ ...prev, ...result }));
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingGetData(false);
    }
  };

  React.useEffect(() => {
    if (tabValue === activeTabs.value) {
      getDataByValueTab();
    }
  }, [tabValue, activeTabs]);

  if (isLoadingGetData) return <div>loading ...</div>;

  return (
    <main className="space-y-2">
      <SectionOne data={aboutUsData.section_1} setData={setAboutUsData} isLoadingGetData={isLoadingGetData} getDataByValueTab={getDataByValueTab} />
      <SectionTwo data={aboutUsData.section_2} setData={setAboutUsData} isLoadingGetData={isLoadingGetData} getDataByValueTab={getDataByValueTab} />
      <SectionThree data={aboutUsData.section_3} setData={setAboutUsData} isLoadingGetData={isLoadingGetData} getDataByValueTab={getDataByValueTab} />
    </main>
  );
}

type TSectionOne = {
  data: Section1;
  setData?: React.Dispatch<React.SetStateAction<AboutUsData>>;
  isLoadingGetData: boolean;
  getDataByValueTab?: () => void;
};

function SectionOne({ data, setData, isLoadingGetData, getDataByValueTab }: TSectionOne) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleUploadImage = async (files) => {
    try {
      const formData = new FormData();
      formData.append('file', files);
      formData.append('page_name', activeTabs.value);
      const response = await services.post('/setting/company_profile/upload-file', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      if (response.statusCode === 200) {
        toast.success('success upload image');
        const result = response;
        return result;
      }
    } catch (error) {
      toast.error(error.message);
      console.error(error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoadingSubmit(true);

    const payload = {
      page_name: activeTabs.value,
      card_group: 'section_1',
      data: data,
    };

    try {
      // Mendeteksi dan meng-upload gambar jika ada
      const uploadPromises = [];
      const traverseData = async (obj) => {
        for (const key in obj) {
          if (obj[key] instanceof File || obj[key] instanceof Blob) {
            // Jika key adalah objek File (gambar), upload dulu
            uploadPromises.push(
              handleUploadImage(obj[key])
                .then((result) => {
                  if (result) {
                    // Update value gambar dengan hasil URL dari upload
                    obj[key] = result.filename; // Sesuaikan dengan respons URL dari API
                  } else {
                    throw new Error('Gagal meng-upload gambar');
                  }
                })
                .catch((error) => {
                  throw new Error(error.message);
                })
            );
          } else if (typeof obj[key] === 'object' && obj[key] !== null) {
            // Jika ada nested object, lakukan rekursi
            await traverseData(obj[key]);
          }
        }
      };
      await traverseData(payload.data);
      // Tunggu semua proses upload selesai
      await Promise.all(uploadPromises);
      // Jika semua upload berhasil, lakukan submit
      const response = await services.post('/setting/company_profile/save', payload);
      if (response.statusCode === 200) {
        getDataByValueTab();
        setIsEdit(false);
        setIsOpenModalSuccess(true);
      }
    } catch (error) {
      toast.error(error.message);
      console.error(error);
    } finally {
      setIsLoadingSubmit(false);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <Collapsible isEdit={isEdit} setIsEdit={setIsEdit} isLoading={isLoadingSubmit} footer={!isLoadingGetData} title="Section 1">
        {isEdit ? (
          <FormSectionOne data={data} setData={setData} isLoadingGetData={isLoadingGetData} />
        ) : (
          <ViewSectionOne data={data} isLoadingGetData={isLoadingGetData} />
        )}
      </Collapsible>
    </form>
  );
}

function FormSectionOne({ data, setData, isLoadingGetData }: TSectionOne) {
  const handleInputChange = (e, index?) => {
    const { name, files, value } = e.target;

    if (files && files.length > 0) {
      const fileSizeInMB = files[0].size / 1024 / 1024; // Mengonversi byte menjadi MB

      if (fileSizeInMB > 1) {
        toast.error('Filesize exceed 1 MB');
        e.target.value = ''; // Reset file input agar nama file tidak muncul
        return;
      }

      setData((prev) => {
        const updateAboutImage = [...prev.section_1.about_image];

        updateAboutImage[index].about_image_src = files[0];

        return {
          ...prev,
          section_1: {
            ...prev.section_1,
            about_image: updateAboutImage,
          },
        };
      });

      return;
    }
    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        [name]: value,
      },
    }));
  };

  const handleRteChangeInput = (index?: number, name?: string, value?: string) => {
    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        [name]: value,
      },
    }));
  };

  const handleRemoveList = (index: number) => {
    setData((prev) => {
      const updateAboutImage = [...prev.section_1.about_image];

      updateAboutImage[index].about_image_src = '';

      return {
        ...prev,
        section_1: {
          ...prev.section_1,
          about_image: updateAboutImage,
        },
      };
    });
  };

  return (
    <div className="p-4 text-sm space-y-4">
      {/* Title */}
      <div>
        <label htmlFor="title" className="w-full">
          Title
        </label>
        {/* <p className="">Title</p> */}
        <input
          name="about_title"
          onChange={handleInputChange}
          value={data.about_title}
          id="about_title"
          type="text"
          placeholder="Welcome to the Humavue Platform"
          className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
        />
      </div>

      {/* Description */}
      <div>
        <label htmlFor="description" className="w-full">
          Description
        </label>
        <div className="mt-1.5">
          <RichTextEditor contentHtml={data.about_content} name="about_content" onChange={handleRteChangeInput} />
        </div>
      </div>

      {/* Image 1*/}
      {data.about_image.map((item, index) => (
        <div key={index}>
          <label htmlFor="upload_image_1" className="w-full flex items-center gap-x-1">
            <p>Upload Image {index + 1}</p>
            <IoIosInformationCircleOutline className="size-4 text-slate-500" />
            <p className="text-red-500 font-normal text-xs">*Format jpg/png/jpeg. max 1MB</p>
          </label>

          <InputFile
            onRemove={() => handleRemoveList(index)}
            index={index}
            type="file"
            name="about_image_src"
            onChange={(e) => handleInputChange(e, index)}
            value={item.about_image_src}
          />
        </div>
      ))}
    </div>
  );
}

function ViewSectionOne({ data, isLoadingGetData }: TSectionOne) {
  return (
    <div className="p-4 text-sm space-y-4">
      {/* Title */}
      <div className="space-y-1">
        <p className="font-semibold">Title</p>
        <p className="">{data.about_title}</p>
      </div>

      {/* Description */}
      <div className="space-y-1">
        <p className="font-semibold">Description</p>
        <div dangerouslySetInnerHTML={{ __html: data.about_content }} />
      </div>

      {/* Image 1*/}
      <div className="space-y-1">
        {data.about_image.map((item, index) => (
          <React.Fragment key={index}>
            <p className="font-semibold">
              <p>Upload Image {index + 1}</p>
            </p>
            <img className="max-w-56" src={item.about_image_src} alt={`upload_image_${index + 1}`} />
          </React.Fragment>
        ))}
      </div>
    </div>
  );
}

type TSectionTwo = {
  data: Section2;
  setData?: React.Dispatch<React.SetStateAction<AboutUsData>>;
  isLoadingGetData: boolean;
  getDataByValueTab?: () => void;
};

function SectionTwo({ data, setData, isLoadingGetData, getDataByValueTab }: TSectionTwo) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoadingSubmit(true);

    const payload = {
      page_name: activeTabs.value,
      card_group: 'section_2',
      data: data,
    };

    try {
      const response = await services.post('/setting/company_profile/save', payload);
      if (response.statusCode === 200) {
        getDataByValueTab();
        setIsEdit(false);
        setIsOpenModalSuccess(true);
      }
    } catch (error) {
      toast.error(error.message);
      console.error(error);
    } finally {
      setIsLoadingSubmit(false);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <Collapsible isLoading={isLoadingSubmit} isEdit={isEdit} setIsEdit={setIsEdit} footer={!isLoadingGetData} title="Section 2">
        {isEdit ? (
          <FormSectionTwo data={data} setData={setData} isLoadingGetData />
        ) : (
          <ViewSectionTwo data={data} isLoadingGetData={isLoadingGetData} />
        )}
      </Collapsible>
    </form>
  );
}

function FormSectionTwo({ data, setData, isLoadingGetData }: TSectionTwo) {
  const handleChangeInput = (e) => {
    const { name, value } = e.target;

    setData((prev) => ({
      ...prev,
      section_2: {
        ...prev.section_2,
        [name]: value,
      },
    }));
  };

  const handleInputChangeCulture = (index?, name?, updatedContent?) => {
    setData((prev) => {
      const updatedCompanyCulture = [...prev.section_2.company_culture];

      updatedCompanyCulture[index] = {
        ...updatedCompanyCulture[index],
        culture: {
          ...updatedCompanyCulture[index].culture,
          [name]: updatedContent,
        },
      };
      return {
        ...prev,
        section_2: {
          ...prev.section_2,
          company_culture: updatedCompanyCulture,
        },
      };
    });
  };

  const handleAddMore = () => {
    setData((prev) => ({
      ...prev,
      section_2: {
        ...prev.section_2,
        company_culture: [...prev.section_2.company_culture, { culture: { title: '', body: '' } }],
      },
    }));
  };

  const handleRemoveList = (index: number) => {
    setData((prev) => ({
      ...prev,
      section_2: {
        ...prev.section_2,
        company_culture: prev.section_2.company_culture.filter((_, i) => i !== index),
      },
    }));
  };

  return (
    <div className="p-4 text-sm space-y-4">
      {/* Title */}
      <div>
        <label htmlFor="title" className="w-full">
          Title
        </label>
        <input
          onChange={handleChangeInput}
          value={data.head_title_1}
          name="head_title_1"
          id="title"
          type="text"
          placeholder="Welcome to the Humavue Platform"
          className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
        />
      </div>

      {/* Subtitle */}
      <div>
        <label htmlFor="subtitle" className="w-full">
          Subtitle
        </label>
        {/* <p className="">Title</p> */}
        <input
          onChange={handleChangeInput}
          value={data.head_subtitle_1}
          name="head_subtitle_1"
          id="subtitle"
          type="text"
          placeholder="Add Subtitle"
          className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
        />
      </div>

      <div className="flex items-center justify-between">
        <div className="flex-1 flex items-center justify-start mb-1.5">
          <p>Descriptions and Details</p> <p className="text-red-500 text-xs">*Add at least 3 descriptions and details to be able to save</p>
        </div>
      </div>
      {/* Description */}
      {data.company_culture.map((item, index) => (
        <div key={index}>
          <div className="flex items-center justify-between">
            <label>{index + 1}. Description</label>
            <button
              onClick={() => handleRemoveList(index)}
              type="button"
              className="p-0.5 rounded-md hover:bg-gray-200 text-slate-600 disabled:bg-gray-100 disabled:cursor-not-allowed disabled:text-gray-400"
            >
              <LuTrash2 className="size-4" />
            </button>
          </div>

          <input
            onChange={(e) => handleInputChangeCulture(index, 'title', e.target.value)}
            value={item.culture.title}
            name="title"
            id="description"
            type="text"
            placeholder="Add description"
            className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
          />
          <div className="mt-1.5">
            <RichTextEditor contentHtml={item.culture.body} name="body" index={index} onChange={handleInputChangeCulture} />
          </div>
        </div>
      ))}

      <div className="flex items-center justify-end">
        <Button
          onClick={handleAddMore}
          type="button"
          variant="outlined"
          size="sm"
          className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
        >
          Add More
          <FaPlus className="size-3 text-blue-600" />
        </Button>
      </div>
    </div>
  );
}

function ViewSectionTwo({ data, isLoadingGetData }: TSectionTwo) {
  return (
    <div className="p-4 text-sm space-y-4">
      {/* Title */}
      <div className="space-y-1">
        <p className="font-semibold">Title</p>
        <p className="">{data.head_title_1}</p>
      </div>

      {/* Subtitle */}
      <div>
        <p className="font-semibold">Subtitle</p>
        <p className="">{data.head_subtitle_1}</p>
      </div>

      {/* Description */}
      <div>
        <div className="mb-1.5">
          <p className="font-semibold">Descriptions</p>
        </div>

        <ul className="space-y-2 list-decimal px-4">
          {data.company_culture.map((item, index) => (
            <li className="space-y-1" key={index}>
              <h5>{item.culture.title}</h5>
              <div className="text-xs font-medium text-gray-500" dangerouslySetInnerHTML={{ __html: item.culture.body }} />
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

