import * as React from 'react';

type TModalConfirmationContext = {
  isOpenModalConfirmation: boolean;
  setIsOpenModalConfirmation: React.Dispatch<React.SetStateAction<boolean>>;
  onConfirm: () => void;
  setOnConfirm: React.Dispatch<React.SetStateAction<() => void>>;
  handleClosed: () => void;
  content: string;
  setContent: React.Dispatch<React.SetStateAction<string>>;
};

export const ModalConfirmationContext = React.createContext<TModalConfirmationContext | undefined>(undefined);

export const useModalConfirmationContext = () => {
  const context = React.useContext(ModalConfirmationContext);
  if (context === undefined) {
    throw new Error('useModalConfirmationContext must be used within a ModalConfirmationProvider');
  }
  return context;
};

type TModalConfirmationProviderProps = {
  children: React.ReactNode;
};

export const ModalConfirmationProvider = ({ children }: TModalConfirmationProviderProps) => {
  const [isOpenModalConfirmation, setIsOpenModalConfirmation] = React.useState(false);
  const [onConfirm, setOnConfirm] = React.useState<() => void>(() => {});
  const [content, setContent] = React.useState<string>('');

  const handleClosed = () => {
    setIsOpenModalConfirmation(false);
    setOnConfirm(() => () => {}); // Clear the onConfirm state
    setContent('');
  };

  return (
    <ModalConfirmationContext.Provider
      value={{ isOpenModalConfirmation, setIsOpenModalConfirmation, onConfirm, setOnConfirm, handleClosed, content, setContent }}
    >
      {children}
    </ModalConfirmationContext.Provider>
  );
};
