import * as React from 'react';
import { Collapse, Button, Card, Typography, CardBody } from '@material-tailwind/react';
import { clsx } from 'clsx';
import { IoChevronDownOutline } from 'react-icons/io5';
import { FaSave } from 'react-icons/fa';
import { useEditCollapseContext } from '../../context/editCollapseContext';
import { RiFileEditLine } from 'react-icons/ri';

type TCollapsible = {
  title: string;
  children: React.ReactNode;
  footer?: boolean;
  preview?: boolean;
  isLoading?: boolean;
  isEdit?: boolean;
  setIsEdit?: React.Dispatch<React.SetStateAction<boolean>>;
  isShowPreview?: boolean;
};

export default function Collapsible({ title, children, footer, preview, isLoading, isEdit, setIsEdit, isShowPreview }: TCollapsible) {
  //! Preview Belum, lanjutkan!!
  // const { isEdit, setIsEdit } = useEditCollapseContext();
  const [open, setOpen] = React.useState(true);

  const toggleOpen = () => setOpen((cur) => !cur);

  return (
    <div className={clsx('border rounded-lg overflow-hidden')}>
      <div className="cursor-pointer px-3 py-3 bg-muted-50" onClick={toggleOpen}>
        <div className="flex justify-between items-center">
          <div className="space-x-3 flex items-center">
            <Typography className="flex-1 text-slate-800 font-medium text-sm">{title}</Typography>
          </div>
          <IoChevronDownOutline />
        </div>
      </div>

      <Collapse className="h-full" open={open}>
        <div className="border-t">{children}</div>

        {footer && (
          <React.Fragment>
            {isEdit && (
              <div className="flex items-center justify-between border-t px-5 py-4">
                <Button
                  loading={isLoading}
                  disabled={isLoading}
                  type="button"
                  onClick={() => setIsEdit(false)}
                  variant="outlined"
                  size="sm"
                  className="normal-case text-sm rounded text-blue-600 border-blue-600"
                >
                  Cancel
                </Button>
                <Button
                  loading={isLoading}
                  disabled={isLoading}
                  type="submit"
                  variant="filled"
                  size="sm"
                  className="normal-case text-sm rounded flex items-center justify-center space-x-2 bg-primary-500 text-white hover:shadow-none shadow-none hover:bg-blue-500"
                >
                  <FaSave className="size-4" />
                  <p>{isLoading ? 'Loading' : 'Save Changes'}</p>
                </Button>
              </div>
            )}

            {isEdit === false && (
              <div className="flex items-center justify-end border-t px-5 py-4 space-x-2">
                {!isShowPreview && (
                  <Button type="button" variant="text" size="sm" className="normal-case text-sm rounded text-primary-500">
                    Preview
                  </Button>
                )}
                <Button
                  type="button"
                  onClick={() => setIsEdit(true)}
                  variant="outlined"
                  size="sm"
                  className="normal-case text-sm rounded text-primary-500 border-primary-500 flex items-center justify-center space-x-3"
                >
                  <RiFileEditLine className="size-4" />
                  <p>Edit Data</p>
                </Button>
              </div>
            )}
          </React.Fragment>
        )}
      </Collapse>
    </div>
  );
}
