import * as React from 'react';
import { Tabs, TabsHeader, TabsBody, Tab, TabPanel } from '@material-tailwind/react';
import { clsx } from 'clsx';
import FAQ from '../content-management-system/FAQ';
import PrivacyPolicy from '../content-management-system/PrivacyPolicy';
import TermsConditions from '../content-management-system/TermsConditions';
import AboutUs from '../content-management-system/AboutUs';
import HeaderFooter from '../content-management-system/HeaderFooter';
import Home from '../content-management-system/Home';
import { usePathViewContext } from '../../context/pathViewContext';

export default function ContentTabs() {
  const [activeTab, setActiveTab] = React.useState('headers_footers');
  const { setActiveTabs } = usePathViewContext();

  const tabs = [
    {
      label: 'Header & Footer',
      value: 'headers_footers',
      content: <HeaderFooter tabValue="headers_footers" />,
      path: 'home',
    },
    {
      label: 'Home',
      value: 'home',
      content: <Home tabValue="home" />,
      path: 'home',
    },
    {
      label: 'About Us',
      value: 'about',
      content: <AboutUs tabValue="about" />,
      path: 'about',
    },

    {
      label: 'FAQ',
      value: 'faq',
      content: <FAQ tabValue="faq" />,
      path: 'faq',
    },
    {
      label: 'Privacy Policy',
      value: 'privacy_policy',
      content: <PrivacyPolicy tabValue="privacy_policy" />,
      path: 'privacy-policy',
    },
    {
      label: 'Terms and Conditions',
      value: 'tnc',
      content: <TermsConditions />,
      path: 'terms-conditions',
    },
  ];

  const handleClickActiveTabs = async (valueTab: string, index: number) => {
    if (activeTab === valueTab) return;

    setActiveTab(valueTab);
    setActiveTabs(tabs[index]);
  };

  const findTabs = (tabs: any) => {
    const tabFound = tabs.find((item) => item.value === activeTab);
    return tabFound;
  };

  React.useEffect(() => {
    setActiveTabs(tabs[0]);
  }, []);

  return (
    <Tabs value={activeTab} className="rounded-lg overflow-hidden bg-muted-50 border">
      <TabsHeader
        className="p-0 border-b rounded-none bg-muted-50"
        indicatorProps={{
          className: 'bg-muted-50 border-b-2 border-primary-500 shadow-none rounded-none',
        }}
      >
        {tabs.map((tab, index) => (
          <Tab
            key={tab.value}
            value={tab.value}
            onClick={() => handleClickActiveTabs(tab.value, index)}
            className={clsx('w-fit px-5 py-3.5 text-sm ', activeTab === tab.value ? 'text-primary-500 font-bold' : 'text-black font-normal')}
          >
            {tab.label}
          </Tab>
        ))}
      </TabsHeader>
      <TabsBody className="bg-white">
        {tabs.map((tab) => (
          <TabPanel key={tab.value} value={tab.value} className="p-5">
            <div className="bg-white text-sm font-normal">{tab.content}</div>
          </TabPanel>
        ))}
      </TabsBody>
    </Tabs>
  );
}
