import * as React from 'react';

declare global {
  interface Window {
    RichTextEditor: any;
  }
}

type T_RTE = {
  contentHtml?: string | null;
  setContentHtml?: any;
  name?: string;
  onChange?: (index: number, name: string, value: string) => void;
  index?: number;
  setData?: any;
};

export default function RichTextEditor({ contentHtml, setContentHtml, name, onChange, index, setData }: T_RTE) {
  const refdiv = React.useRef<HTMLDivElement | null>(null);
  const rteInstance = React.useRef<any>(null); // Ref to hold the editor instance

  // Initialize the editor only once
  React.useEffect(() => {
    if (refdiv.current && window.RichTextEditor && !rteInstance.current) {
      const editor = new window.RichTextEditor(refdiv.current);
      rteInstance.current = editor;

      editor.setHTMLCode(`<div style="font-family: 'Inter', sans-serif;">${contentHtml}</div>`);

      // Adding onChange event listener
      editor.attachEvent('change', function () {
        const updatedContent = editor.getHTMLCode();

        if (setContentHtml) {
          setContentHtml((prev) => ({ ...prev, [name]: updatedContent }));
          return;
        }

        if (onChange) {
          onChange(index, name, updatedContent); // Pass updatedContent to onChange callback
        }
      });
    }

    // Cleanup on unmount
    return () => {
      if (rteInstance.current) {
        rteInstance.current.detachEvent('change');
      }
    };
  }, [setContentHtml]);

  // Update the editor content only if contentHtml changes
  React.useEffect(() => {
    if (rteInstance.current && contentHtml !== rteInstance.current.getHTMLCode()) {
      rteInstance.current.setHTMLCode(`<div style="font-family: 'Inter', sans-serif;">${contentHtml}</div>`);
    }
  }, [contentHtml]);

  return <div className="text-sm" ref={refdiv} />;
}
