import axios from 'axios';
import sessions from '../sessions';
import {getPackage} from '../services/packageTS';

// console.log(process.env.REACT_APP_API_URL)
async function fetchJson() {
  const definePackage = getPackage(process.env.NODE_ENV);
  const response = await fetch(definePackage);
  return response.json();
}

const finalUrl = await fetchJson().then(data => { return data.url; });

const axiosInstance = axios.create({
  baseURL: 'https://'+finalUrl, // 'https://api-dev-v2.humanvue.id', // process.env.REACT_APP_API_URL, //'https://apiwr.infomedia.co.id',
  headers: {
    'Content-Type': 'application/json',
  },
});

axiosInstance.interceptors.request.use(
  (config) => {
    const token = sessions.getSessionToken();
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

axiosInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error.response) {
      const { status, config } = error.response;
      if (status === 401 || status === 403) {
        localStorage.clear();
        window.location.replace('/login');
      }
      if (status === 404) {
        window.location.replace('/404');
      }
    }
    // if (error.response.status === 401) {
    //   // Handle unauthorized access, e.g., redirect to login
    // }
    return Promise.reject(error);
  }
);

export default axiosInstance;
