export const getPackage = (env: string) => {
    const domain = typeof window !== 'undefined' ? window.location.hostname : 'localhost';
    var tenant = 'product';
    if (domain.includes('humanvue') && domain.split('-')[0] != 'wr'){
        tenant = domain.split('-')[0];
    }
    if (domain.includes('vm.humanvue.id')){
        tenant = 'product-vm';
    }
    
    let appConfig = `https://storage.googleapis.com/pkg-wr/pkg-json/${env}-${tenant}.json`;
    return appConfig;
}