import * as React from 'react';
import ContentTabs from '../content-tabs';
import { Button, Dialog, DialogHeader, DialogBody, DialogFooter, Typography } from '@material-tailwind/react';
import { useModalConfirmationContext } from '../../context/confirmationContext';
import { useSuccessModalContext } from '../../context/successModalContext';
import { usePathViewContext } from '../../context/pathViewContext';
import { IoEyeOutline } from 'react-icons/io5';
import { FaSearch } from 'react-icons/fa';
import { HiMiniXMark } from 'react-icons/hi2';

export default function Main() {
  const { setIsOpenModalConfirmation, setOnConfirm, handleClosed, setContent } = useModalConfirmationContext();
  const { isOpenModalSuccess, setIsOpenModalSuccess, handleClosedModalSuccess } = useSuccessModalContext();

  const handleOpenModal = () => {
    setIsOpenModalConfirmation(true);
    setOnConfirm(() => () => {
      setIsOpenModalSuccess(true);
      handleClosed();
    });
    setContent('Are you sure you want to delete this field?');
  };

  return (
    <React.Fragment>
      <div className="space-y-2">
        <Headers />
        <ContentTabs />
      </div>

      {/* <button onClick={handleOpenModal}>Open Modal Confirmation</button> */}
      {/* <ModalConfirmation /> */}
      <ModalSuccess />
    </React.Fragment>
  );
}

function Headers() {
  const [isOpenView, setIsOpenView] = React.useState<boolean>(false);

  const handleOpenView = () => {
    setIsOpenView(true);
  };

  return (
    <React.Fragment>
      <header className="px-4 py-3.5 rounded-lg bg-white flex items-center justify-between">
        <h3 className="font-bold text-sm">Settings</h3>
        <Button
          onClick={handleOpenView}
          variant="outlined"
          size="sm"
          className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
        >
          <IoEyeOutline className="size-4 text-blue-600" />
          View
        </Button>
      </header>

      <ModalView isOpen={isOpenView} setIsOpen={setIsOpenView} />
    </React.Fragment>
  );
}

function ModalConfirmation() {
  const { isOpenModalConfirmation, setIsOpenModalConfirmation, onConfirm, handleClosed, content } = useModalConfirmationContext();

  const handleConfirm = () => {
    if (onConfirm) {
      onConfirm();
      return;
    }
  };

  return (
    <Dialog size="sm" open={isOpenModalConfirmation}>
      <DialogBody>
        <div className="flex flex-col items-center justify-center gap-y-5">
          <IconConfirmation />

          {/* <h3 className="font-semibold text-sm text-gray-900">Are you sure you want to delete this field?</h3> */}
          <h3 className="font-semibold text-sm text-gray-900">{content}</h3>

          <div className="flex items-center justify-center space-x-4">
            <Button onClick={handleClosed} className="w-36 normal-case border border-gray-300 focus:ring-0 focus:outline-none" variant="outlined">
              Cancel
            </Button>
            <Button
              onClick={handleConfirm}
              className="w-36 normal-case bg-primary-500 text-white hover:bg-primary-400 shadow-none hover:shadow-none focus:ring-0 focus:outline-none"
              variant="filled"
            >
              Confirm
            </Button>
          </div>
        </div>
      </DialogBody>
    </Dialog>
  );
}

