function ModalSuccess() {
  const { isOpenModalSuccess, setIsOpenModalSuccess, handleClosedModalSuccess } = useSuccessModalContext();
  const { activeTabs } = usePathViewContext();

  return (
    <Dialog size="sm" open={isOpenModalSuccess}>
      <DialogBody className="p-0 rounded-lg overflow-hidden">
        <div className="flex flex-col items-center justify-center">
          <div className="flex items-center justify-center py-5 w-full bg-[#FBF7EB]">
            <SuccessIcon />
          </div>
          <div className="p-4 text-center space-y-5 text-black">
            <h2 className="font-bold text-xl">Saved</h2>
            <p className="font-normal text-sm">{activeTabs.label} has been successfully saved.</p>
            <Button
              onClick={handleClosedModalSuccess}
              className="w-36 normal-case bg-primary-500 text-white hover:bg-primary-400 shadow-none hover:shadow-none focus:ring-0 focus:outline-none"
              variant="filled"
            >
              Close
            </Button>
          </div>
        </div>
      </DialogBody>
    </Dialog>
  );
}

