        d="M109.584 61.154c-9.232 1.297-18.553 1.959-28.029 2.774-5.96 1.203-10.36 6.145-14.056 10.972-2.746 3.586-5.524 7.401-9.146 10.099a2.304 2.304 0 00.856 4.073c1.396.347 2.824.55 4.262.604 3.188.108 6.456-.641 9.083-2.45 2.025-1.394 3.59-3.343 5.192-5.098a36.772 36.772 0 0021.758 3.917c3.117-.406 6.329-1.298 8.512-2.747 1.417-2.927 3.527-5.508 4.662-8.555 1.79-4.802.824-10.526-3.094-13.589z"
