import * as React from 'react';
import Collapsible from '../../collapsible';
import { Switch } from '@material-tailwind/react';
import { IoIosInformationCircleOutline } from 'react-icons/io';
import RichTextEditor from '../../richtexteditor';
import { usePathViewContext } from '../../../context/pathViewContext';
import { useSuccessModalContext } from '../../../context/successModalContext';
import services from '../../../../../../../utils/services';

export default function PrivacyPolicy({ tabValue }: { tabValue: string }) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const [toggleTopic, setToggleTopic] = React.useState<boolean>(false);

  const [privacyPolicyData, setPrivacyPolicyData] = React.useState<any>(null);
  const [isLoadingGetData, setIsLoadingGetData] = React.useState<boolean>(true);

  const getDataByValueTab = async () => {
    setIsLoadingGetData(true);
    try {
      const payload = {
        page_name: tabValue,
      };
      const response = await services.post('/setting/company_profile', payload);
      const result = response.data;
      setPrivacyPolicyData((prev) => ({ ...prev, ...result }));
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingGetData(false);
    }
  };

  React.useEffect(() => {
    if (tabValue === activeTabs.value) {
      getDataByValueTab();
    }
  }, [tabValue, activeTabs]);

  if (isLoadingGetData) return <div>loading ...</div>;

  const handleChangeToggleTopic = () => {
    setToggleTopic(!toggleTopic);
  };

  return (
    <main className="space-y-3">
      <Header />
      <Collapsible isEdit={isEdit} setIsEdit={setIsEdit} footer={!isLoadingGetData && toggleTopic} title="Privacy Policy">
        <div className="p-4 text-sm space-y-4">
          <div className="flex items-center justify-between rounded-lg border border-blue-700 px-10 py-4">
            <div className="space-y-1 text-black">
              <div className="flex items-center space-x-2">
                <h5 className="font-semibold">Turn on Privacy Policy</h5>
                <IoIosInformationCircleOutline className="ml-1 size-4 text-slate-500" />
              </div>
              <p className="text-xs">You can choose to enable or disable Privacy and Policy according to your needs.</p>
            </div>

            <div className="flex items-center space-x-3">
              <Switch
                onChange={handleChangeToggleTopic}
                ripple={false}
                color="blue"
                checked={toggleTopic}
                circleProps={{
                  className: 'before:hidden z-20',
                }}
              />
              <p className="text-xs">Privacy and Policy {toggleTopic ? 'enabled' : 'disabled'}</p>
            </div>
          </div>

          {toggleTopic && <React.Fragment>{isEdit ? <FormPrivacyPolicy /> : <ViewPrivacyPolicy />}</React.Fragment>}
          {!toggleTopic && <DisabledComponent />}
        </div>
      </Collapsible>
    </main>
  );
}

function FormPrivacyPolicy() {
  return (
    <React.Fragment>
      <div>
        <label htmlFor="current_version" className="w-full">
          Current Version
        </label>
        <input
          id="current_version"
          type="text"
          placeholder="eg. Version 1.0"
          className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
        />
        <div className="flex items-center justify-between text-[11px] text-gray-400">
          <p>Write the new version name according to your needs.</p>
          <p>0/50</p>
        </div>
      </div>

      <div>
        <label htmlFor="current_version" className="w-full">
          Custom Privacy Policy
        </label>
        <div className="mt-1.5">
          <RichTextEditor contentHtml="" />
        </div>
      </div>
    </React.Fragment>
  );
}

function ViewPrivacyPolicy() {
  const [isExpanded, setIsExpanded] = React.useState<boolean>(false);

  return (
    <React.Fragment>
      <div>
        <label htmlFor="current_version" className="w-full">
          Current Version
        </label>
        <p className="">Version 2.0</p>
        {/* <input
                  id="current_version"
                  type="text"
                  placeholder="eg. Version 1.0"
                  className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                />
                <div className="flex items-center justify-between text-[11px] text-gray-400">
                  <p>Write the new version name according to your needs.</p>
                  <p>0/50</p>
                </div> */}
      </div>

      <div>
        <label htmlFor="current_version" className="w-full">
          Custom Privacy Policy
        </label>
        <div className="mt-1.5">
          {/* <RichTextEditor contentHtml="" /> */}

          <React.Fragment>
            <div dangerouslySetInnerHTML={{ __html: isExpanded ? termsAndConditionsHTML : termsAndConditionsHTML.substring(0, 500) + '...' }} />

            <div className="flex items-center justify-end">
              <button onClick={() => setIsExpanded(!isExpanded)} className="text-blue-600">
                {isExpanded ? 'Read Less' : 'Read More'}
              </button>
            </div>
          </React.Fragment>
        </div>
      </div>
    </React.Fragment>
  );
}

function Header() {
  return (
    <div className="px-5 py-3 rounded-lg bg-slate-50 space-y-3">
      <div className="text-xs space-y-0.5">
        <h5 className="font-semibold text-sm">Privacy Policy</h5>
        <p>This platform lets companies show their Privacy Policy to candidates. </p>
        <p>This document explains how we protect and use your personal information</p>
      </div>
      <div>
        <h5 className="font-semibold mb-1 text-sm">How to Create Your Privacy Policy:</h5>
        <ul className="list-decimal pl-5 text-xs space-y-0.5">
          <li>Enter your company's Privacy Policy in the section</li>
          <li>Click "Preview" to review it</li>
          <li>Click "Save Changes" to apply your Privacy Policy</li>
        </ul>
      </div>
    </div>
  );
}

function DisabledComponent() {
  return (
    <div className="flex items-center justify-center pt-10">
      <div className="text-center max-w-3xl space-y-5">
        <p>
          You have chosen not to activate/use Privacy and Policy. You do not need to configure anything if you choose to keep privacy and policy
          disabled.
        </p>
        <p className="font-semibold">If you wish to activate it, please click the 'On' button above.</p>
      </div>
    </div>
  );
}

const termsAndConditionsHTML = `
  <div style="font-family: Arial, sans-serif; color: #333; line-height: 1.6;">
    <h1 style="text-align: center; font-size: 24px; font-weight: bold;">Penerimaan Syarat dan Ketentuan</h1>
    <p style="text-align: center;">Dengan menggunakan platform ini, Anda setuju untuk mematuhi syarat dan ketentuan yang telah ditetapkan.<br/>Jika Anda tidak setuju, harap tidak melanjutkan penggunaan.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Penggunaan Layanan</h2>
    <p style="margin: 0;">a. Anda bertanggung jawab atas keamanan dan kerahasiaan informasi akun Anda.</p>
    <p style="margin: 0;">b. Segera laporkan kepada kami jika Anda mencurigai adanya akses yang tidak sah ke akun Anda.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Akun Pengguna</h2>
    <p style="margin: 0;">a. Anda bertanggung jawab atas keamanan dan kerahasiaan informasi akun Anda.</p>
    <p style="margin: 0;">b. Segera laporkan kepada kami jika Anda mencurigai adanya akses yang tidak sah ke akun Anda.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Konten Pengguna</h2>
    <p style="margin: 0;">a. Hargai hak kekayaan intelektual. Jangan mengunggah atau menyebarkan materi yang melanggar hak cipta.</p>
    <p style="margin: 0;">b. Kami berhak menghapus konten yang dianggap tidak sesuai atau melanggar norma.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Interaksi dan Etika</h2>
    <p style="margin: 0;">a. Hormati pendapat dan perbedaan. Hindari perilaku yang merugikan atau mengganggu pengguna lain.</p>
    <p style="margin: 0;">b. Larangan tegas atas pelecehan verbal atau perilaku tidak etis.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Pembayaran</h2>
    <p style="margin: 0;">a. Jika menggunakan layanan berbayar, pastikan melakukan pembayaran sesuai dengan instruksi yang diberikan.</p>
    <p style="margin: 0;">b. Pembatalan langganan dapat dilakukan sesuai dengan kebijakan yang berlaku.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Penghentian Layanan</h2>
    <p style="margin: 0;">a. Kami berhak untuk menangguhkan atau menghentikan akses jika terjadi pelanggaran syarat dan ketentuan.</p>
    <p style="margin: 0;">b. Pemberitahuan akan diberikan sebelum tindakan penghentian diambil.</p>

    <h2 style="font-size: 18px; font-weight: bold;">Perubahan Syarat dan Ketentuan</h2>
    <p style="margin: 0;">a. Perubahan syarat dan ketentuan akan diinformasikan kepada pengguna.</p>
    <p style="margin: 0;">b. Penggunaan terus menerus dianggap sebagai persetujuan terhadap perubahan tersebut.</p>
  </div>
`;
