import * as React from 'react';

type TEditCollapseContext = {
  isEdit: boolean;
  setIsEdit: React.Dispatch<React.SetStateAction<boolean>>;
};

export const EditCollapseContext = React.createContext<TEditCollapseContext | undefined>(undefined);

export const useEditCollapseContext = () => {
  const context = React.useContext(EditCollapseContext);
  if (context === undefined) {
    throw new Error('useEditCollapseContext must be used within a EditCollapseProvider');
  }
  return context;
};

type TEditCollapseProvider = {
  children: React.ReactNode;
};

export const EditCollapseProvider = ({ children }: TEditCollapseProvider) => {
  const [isEdit, setIsEdit] = React.useState(false);

  return <EditCollapseContext.Provider value={{ isEdit, setIsEdit }}>{children}</EditCollapseContext.Provider>;
};
