import * as React from 'react';

type TSuccessModalContext = {
  isOpenModalSuccess: boolean;
  setIsOpenModalSuccess: React.Dispatch<React.SetStateAction<boolean>>;
  handleClosedModalSuccess: () => void;
};

export const SuccessModalContext = React.createContext<TSuccessModalContext | undefined>(undefined);

export const useSuccessModalContext = () => {
  const context = React.useContext(SuccessModalContext);
  if (context === undefined) {
    throw new Error('useSuccessModalContext must be used within a ModalSuccessProvider');
  }
  return context;
};

type TSuccessModalProvider = {
  children: React.ReactNode;
};

export const ModalSuccessProvider = ({ children }: TSuccessModalProvider) => {
  const [isOpenModalSuccess, setIsOpenModalSuccess] = React.useState(false);

  const handleClosedModalSuccess = () => {
    setIsOpenModalSuccess(false);
  };

  return (
    <SuccessModalContext.Provider value={{ isOpenModalSuccess, setIsOpenModalSuccess, handleClosedModalSuccess }}>
      {children}
    </SuccessModalContext.Provider>
  );
};
