import * as React from 'react';
import Collapsible from '../../collapsible';
import { IoIosInformationCircleOutline } from 'react-icons/io';
import { Button, Switch, Menu, MenuHandler, MenuList, MenuItem, Dialog, DialogHeader, DialogBody } from '@material-tailwind/react';
import { FaPlus } from 'react-icons/fa6';
import { HiOutlineDotsVertical } from 'react-icons/hi';
import { MdEdit } from 'react-icons/md';
import { RiDeleteBinLine } from 'react-icons/ri';
import { NoTopicSelected } from './no-topic-selected';
import { IoClose } from 'react-icons/io5';
import RichTextEditor from '../../richtexteditor';
import servicess from '../../../../../../../utils/services';
import { useModalConfirmationContext } from '../../../context/confirmationContext';
import { toast } from 'react-toastify';
import { usePathViewContext } from '../../../context/pathViewContext';
import { useSuccessModalContext } from '../../../context/successModalContext';
import services from '../../../../../../../utils/services';

type Thead_title_1 = string;

type TFaq = {
  faq: {
    head_title_1: Thead_title_1;
  };
};

const initialDataFaq: TFaq = {
  faq: {
    head_title_1: '',
  },
};

export default function FAQ({ tabValue }: { tabValue: string }) {
  const [isOpenDialog, setIsOpenDialog] = React.useState({ status: false, type: '', func: null, isEdit: { status: false, id: null } });
  const [faqData, setFaqData] = React.useState<TFaq>(initialDataFaq);
  const [isLoadingGetData, setIsLoadingGetData] = React.useState<boolean>(true);
  const { activeTabs } = usePathViewContext();

  const getDataByValueTab = async () => {
    setIsLoadingGetData(true);
    try {
      const payload = {
        page_name: tabValue,
      };
      const response = await services.post('/setting/company_profile', payload);
      const result = response.data;
      setFaqData((prev) => ({ ...prev, ...result }));
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingGetData(false);
    }
  };

  React.useEffect(() => {
    if (tabValue === activeTabs.value) {
      getDataByValueTab();
    }
  }, [tabValue, activeTabs]);

  return (
    <React.Fragment>
      <main className="space-y-2">
        <GeneralSettingFAQ data={faqData.faq} setData={setFaqData} isLoadingGetData={isLoadingGetData} getDataByValueTab={getDataByValueTab} />
        <ContentFAQ setIsOpenDialog={setIsOpenDialog} />
      </main>
      <DialogCms
        open={isOpenDialog.status}
        setOpen={setIsOpenDialog}
        type={isOpenDialog.type}
        func={isOpenDialog.func}
        isEdit={isOpenDialog.isEdit}
      />
    </React.Fragment>
  );
}

type TGeneralSettingFAQ = {
  data: any;
  setData: React.Dispatch<React.SetStateAction<TFaq>>;
  isLoadingGetData: boolean;
  getDataByValueTab: () => void;
};

function GeneralSettingFAQ({ data, setData, isLoadingGetData, getDataByValueTab }: TGeneralSettingFAQ) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoadingSubmit(true);

    const payload = {
      page_name: activeTabs.value,
      card_group: 'faq',
      data: data,
    };

    try {
      const response = await services.post('/setting/company_profile/save', payload);
      if (response.statusCode === 200) {
        getDataByValueTab();
        setIsEdit(false);
        setIsOpenModalSuccess(true);
      }
    } catch (error) {
      toast.error(error.message);
      console.error(error);
    } finally {
      setIsLoadingSubmit(false);
    }
  };
  return (
    <form onSubmit={handleSubmit}>
      <Collapsible
        isLoading={isLoadingSubmit}
        isShowPreview
        isEdit={isEdit}
        setIsEdit={setIsEdit}
        footer={!isLoadingGetData}
        title="General Setting FAQ"
      >
        {isEdit ? (
          <FormGeneralFaq data={data} setData={setData} isLoadingGetData={isLoadingGetData} />
        ) : (
          <ViewGeneralFaq data={data} isLoadingGetData={isLoadingGetData} />
        )}
      </Collapsible>
    </form>
  );
}

function ViewGeneralFaq({ data, isLoadingGetData }) {
  return (
    <div className="p-5 space-y-1">
      <p className="font-semibold">Subtitle</p>
      <p>{data.head_title_1}</p>
    </div>
  );
}

function FormGeneralFaq({ data, setData, isLoadingGetData }) {
  const handleChangeInput = (e) => {
    const { name, value } = e.target;

    setData((prev) => ({
      ...prev,
      faq: {
        ...prev.faq,
        [name]: value,
      },
    }));
  };
  return (
    <div className="p-5">
      <div className="flex items-center">
        <label htmlFor="subtitle">Subtitle</label>
        <IoIosInformationCircleOutline className="ml-1 size-4 text-slate-500" />
      </div>
      <input
        onChange={handleChangeInput}
        value={data.head_title_1}
        name="head_title_1"
        id="subtitle"
        type="text"
        className="px-4 py-3 rounded-lg border border-gray-300 text-slate-700 w-full mt-1"
        placeholder="e.g. a list of common questions and answers about a specific topic."
      />
    </div>
  );
}

function ContentFAQ({ setIsOpenDialog }) {
  const [activeTopic, setActiveTopic] = React.useState({ status: true, topic_id: '' });

  const handleClickAdd = (type: string) => {
    setIsOpenDialog((prev) => ({ ...prev, status: true, type: type }));
  };

  return (
    <Collapsible title="FAQ">
      <div className="flex items-start">
        {/* Kiri */}
        <div className="w-2/6 border-r flex-grow h-[80vh]">
          <div className="p-4 flex items-center justify-between">
            <p className="font-bold">
              List Topic : <span className="px-1.5 py-0.5 rounded-full bg-amber-200 text-amber-900 text-xs font-normal">3</span>
            </p>
            <Button
              onClick={() => handleClickAdd('topic')}
              variant="outlined"
              size="sm"
              className="normal-case text-sm text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center space-x-0.5"
            >
              <p>Add</p>
              <FaPlus className="size-3 text-blue-600" />
            </Button>
          </div>

          <hr />
          {/* List Topik FAQ */}
          <div className="overflow-auto h-full pb-[70px]">
            <ListTopic activeTopic={activeTopic} setActiveTopic={setActiveTopic} />
          </div>
        </div>

        {/* Right, Setelah select topik */}
        <div className="w-2/3 flex-grow h-[80vh]">
          <ComponentListFAQ activeTopic={activeTopic} setIsOpenDialog={setIsOpenDialog} />
        </div>
      </div>
    </Collapsible>
  );
}

function ComponentListFAQ({ activeTopic, setIsOpenDialog }) {
  // List FAQ
  const [faqs, setFaqs] = React.useState<any>(null);

  const getListFaq = async () => {
    try {
      const payload = {
        search: '',
        page: 1,
        size: 10,
        sort: '',
      };
      const response = await servicess.post('/setting/faq/list', payload);
      const result = response.data;
      setFaqs(result);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    getListFaq();
  }, []);

  const handleClickAdd = (type: string) => {
    setIsOpenDialog((prev) => ({ ...prev, status: true, type: type, func: getListFaq }));
  };

  return (
    <React.Fragment>
      {!activeTopic.status ? (
        <NoTopicSelected />
      ) : (
        <React.Fragment>
          <div className="p-4 flex items-center justify-between">
            <p className="font-bold">
              List FAQ : <span className="px-1.5 py-0.5 rounded-full bg-amber-200 text-amber-900 text-xs font-normal">{faqs && faqs.length}</span>
            </p>
            <Button
              onClick={() => handleClickAdd('faq')}
              variant="outlined"
              size="sm"
              className="normal-case text-sm text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center space-x-0.5"
            >
              <p>Add</p>
              <FaPlus className="size-3 text-blue-600" />
            </Button>
          </div>

          <hr />

          {/* List FAQ */}
          <div className="overflow-auto h-full pb-[70px]">
            {(!faqs || faqs.length === 0) && (
              <React.Fragment>
                <div>No List FAQ</div>
              </React.Fragment>
            )}
            {faqs && faqs.length > 0 && (
              <React.Fragment>
                {faqs.map((item) => (
                  <ListFaq key={item.id} item={item} getListFaq={getListFaq} setIsOpenDialog={setIsOpenDialog} />
                ))}
              </React.Fragment>
            )}
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

function ListFaq({ item, getListFaq, setIsOpenDialog }) {
  const { handleClosed, setContent, setIsOpenModalConfirmation, setOnConfirm } = useModalConfirmationContext();

  const handleEdit = (id) => {
    setIsOpenDialog((prev) => ({ ...prev, status: true, type: 'faq', func: getListFaq, isEdit: { status: true, id: id } }));
  };

  const handleDelete = async (id) => {
    try {
      const response = await servicess.delete(`/setting/faq/${id}`);

      if (response.statusCode === 200) {
        toast.success(response.message);
        await getListFaq();
        handleClosed();
      }
    } catch (error) {
      console.error(error);
    }
  };

  const onDelete = () => {
    setContent(`Are you sure you want to delete this FAQ List?`);
    setOnConfirm(() => () => {
      handleDelete(item.id);
    });
    setIsOpenModalConfirmation(true);
  };

  return (
    <div className="w-full flex items-center border-b p-4 hover:bg-muted-50">
      <div className="space-y-2">
        <h4 className="font-semibold text-sm">{item.title}</h4>
        <p className="px-2 text-justify">
          (Belum ada Desc dari response) To apply, visit our careers page or the job listing platform where the position is posted. Follow the
          instructions provided, which usually involve submitting your resume and cover letter through an online application system.
        </p>
      </div>
      <div className="flex items-center space-x-2">
        <button onClick={() => handleEdit(item.id)} className="p-1 rounded hover:bg-muted-100">
          <MdEdit className="size-5" />
        </button>
        <button onClick={onDelete} className="p-1 rounded hover:bg-muted-100">
          <RiDeleteBinLine className="size-5" />
        </button>
      </div>
    </div>
  );
}

function ListTopic({ activeTopic, setActiveTopic }) {
  const [toggleTopic, setToggleTopic] = React.useState<boolean>(false);

  const handleRename = (e) => {
    console.log(e);
  };

  const handleDelete = (e) => {
    console.log(e);
  };

  const handleClick = () => {
    setActiveTopic((prev) => ({ ...prev, status: true }));
  };

  const handleChangeToggleTopic = () => {
    setToggleTopic(!toggleTopic);
  };

  return (
    <div onClick={handleClick} className="border-b hover:bg-blue-50 cursor-pointer">
      <div className="flex items-center justify-between py-2 px-4">
        <div className="space-y-1">
          <h4 className="font-semibold text-muted-600">General</h4>
          <p className="text-xs">11 Data FAQ</p>
        </div>
        <div className="flex space-x-2">
          <Switch
            onChange={handleChangeToggleTopic}
            ripple={false}
            color="blue"
            checked={toggleTopic}
            circleProps={{
              className: 'before:hidden z-20',
            }}
          />

          <Menu placement="bottom-end">
            <MenuHandler>
              <button className="px-2 py-2 rounded-md hover:bg-muted-50">
                <HiOutlineDotsVertical className="size-4" />
              </button>
            </MenuHandler>
            <MenuList className="p-1">
              <MenuItem onClick={(e) => handleRename(e)} className="flex items-center justify-between">
                <span>Rename</span> <MdEdit className="size-5" />
              </MenuItem>
              <MenuItem onClick={(e) => handleDelete(e)} className="flex items-center justify-between">
                <span>Delete</span> <RiDeleteBinLine className="size-5" />
              </MenuItem>
            </MenuList>
          </Menu>
        </div>
      </div>
    </div>
  );
}

function DialogCms({ open, setOpen, type, func, isEdit }) {
  const [isLoadingGetDetail, setIsLoadingGetDetail] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState<boolean>(false);
  const [contentHtml, setContentHtml] = React.useState('');
  const [fields, setFields] = React.useState({
    topic_name: '',
  });

  const [fieldsFAQ, setFieldsFAQ] = React.useState({
    title: '',
    description: '',
    sort: '',
  });
  const resetState = () => {
    setFields({ topic_name: '' });
    setFieldsFAQ({
      title: '',
      description: '',
      sort: '',
    });
    setIsLoading(false);
    setContentHtml('');
  };

  const handleCloseDialog = () => {
    resetState();
    setOpen((prev) => ({ ...prev, status: false, func: null, isEdit: { status: false, id: null }, type: '' }));
  };

  const handleSubmitCreateTopic = (e) => {
    e.preventDefault();
    try {
      console.log(fields);
    } catch (error) {
      console.error(error);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (type === 'faq') {
      setFieldsFAQ((prev) => ({ ...prev, [name]: value }));
    } else {
      setFields((prev) => ({ ...prev, [name]: value }));
    }
  };

  // Buat FAQ
  React.useEffect(() => {
    if (isEdit.status && isEdit.id) {
      getDetailFAQ();
    }
  }, [isEdit]);

  const getDetailFAQ = async () => {
    setIsLoadingGetDetail(true);
    try {
      const response = await servicess.get(`/setting/faq/${isEdit.id}`);
      const result = response.data;
      setFieldsFAQ((prev) => ({ ...prev, ...result }));
      setContentHtml(result.description);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingGetDetail(false);
    }
  };

  const handleSubmitCreateFAQ = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    try {
      const payload = {
        title: fieldsFAQ.title,
        description: contentHtml,
        sort: fieldsFAQ.sort,
      };
      const response = await servicess.post('/setting/faq/create', payload);
      if (response.statusCode === 201) {
        toast.success(response.message);
        await func();
        handleCloseDialog();
      }
    } catch (error) {
      // toast.error(error);
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmitEditFAQ = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    try {
      const payload = {
        id: isEdit.id,
        title: fieldsFAQ.title,
        description: contentHtml,
        sort: fieldsFAQ.sort,
      };
      const response = await servicess.post('/setting/faq/update', payload);
      if (response.statusCode === 201) {
        toast.success(response.message);
        await func();
        handleCloseDialog();
      }
    } catch (error) {
      // toast.error(error);
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Dialog size={type === 'faq' ? 'lg' : 'md'} open={open}>
      <DialogHeader className="flex items-center justify-between">
        <h3 className="text-base">
          {isEdit.status ? 'Edit' : 'Add'} {type}
        </h3>
        <button onClick={handleCloseDialog} className="p-1 rounded hover:bg-muted-50">
          <IoClose className="size-5" />
        </button>
      </DialogHeader>
      <hr />
      <div className="normal-case font-normal p-4">
        {type === 'topic' && (
          <form onSubmit={handleSubmitCreateTopic}>
            <div className="flex flex-col gap-y-2">
              <label className="text-sm text-slate-600" htmlFor="topic_faq">
                Topic Name
              </label>
              <input
                value={fields.topic_name}
                onChange={handleChange}
                name="topic_name"
                required
                placeholder="Input Name"
                id="topic_faq"
                type="text"
                className="px-4 py-3 rounded-lg text-sm text-slate-700 border focus:outline-none focus:ring-1 focus:ring-blue-700"
              />
              <div className="flex justify-end mt-3">
                <Button
                  disabled={isLoading}
                  type="submit"
                  loading={isLoading}
                  variant="filled"
                  className="normal-case bg-blue-700 text-white flex items-center justify-center hover:shadow-none hover:bg-blue-600 disabled:bg-gray-200 disabled:text-gray-700 disabled:border"
                >
                  {isLoading ? 'loading' : 'Add New Topic'}
                </Button>
              </div>
            </div>
          </form>
        )}

        {type === 'faq' && (
          <form onSubmit={handleSubmitCreateFAQ}>
            <div className="flex flex-col gap-y-2">
              <div className="flex items-center space-x-3">
                <div className="flex-1">
                  <label className="text-sm text-slate-600" htmlFor="topic_faq">
                    Title FAQ
                  </label>
                  <br />
                  {isLoadingGetDetail ? (
                    <div className="px-4 py-5 bg-gray-200 animate-pulse rounded-lg"></div>
                  ) : (
                    <input
                      value={fieldsFAQ.title}
                      onChange={handleChange}
                      name="title"
                      required
                      placeholder="Add FAQ"
                      id="title"
                      type="text"
                      className="px-4 py-3 rounded-lg text-sm text-slate-700 border focus:outline-none focus:ring-1 focus:ring-blue-700 w-full"
                    />
                  )}
                </div>
                <div className="w-[10%]">
                  <label className="text-sm text-slate-600" htmlFor="topic_faq">
                    Sort FAQ
                  </label>
                  <br />
                  {isLoadingGetDetail ? (
                    <div className="px-4 py-5 bg-gray-200 animate-pulse rounded-lg"></div>
                  ) : (
                    <input
                      value={fieldsFAQ.sort}
                      onChange={handleChange}
                      name="sort"
                      required
                      placeholder="Eg. 1"
                      id="sort"
                      type="text"
                      className="text-center px-4 py-3 rounded-lg text-sm text-slate-700 border focus:outline-none focus:ring-1 focus:ring-blue-700 w-full"
                      inputMode="numeric"
                    />
                  )}
                </div>
              </div>
              <div>
                <label className="text-sm text-slate-600" htmlFor="deskripsi_faq">
                  Deskripsi FAQ
                </label>
                {isLoadingGetDetail ? (
                  <div className="w-full h-32 bg-gray-200 animate-pulse rounded-lg"></div>
                ) : (
                  <RichTextEditor contentHtml={contentHtml} setContentHtml={setContentHtml} />
                )}
              </div>
              <div className="flex justify-end mt-3">
                <Button
                  disabled={isLoading || isLoadingGetDetail}
                  type="submit"
                  loading={isLoading}
                  variant="filled"
                  className="normal-case bg-blue-700 text-white flex items-center justify-center hover:shadow-none hover:bg-blue-600 disabled:bg-gray-200 disabled:text-gray-700 disabled:border"
                >
                  {isLoading ? 'loading' : isEdit ? 'Save FAQ' : 'Add FAQ'}
                </Button>
              </div>
            </div>
          </form>
        )}
      </div>
    </Dialog>
  );
}
